<template>
  <div class="settings">
    <label for="column">列</label
    ><input type="number" v-model="column" id="column" /><br />
    <label for="pos">位置</label>
    <input type="text" v-model="posInput" id="pos" /><br />
    <label for="name">名字</label>
    <input type="text" v-model="nameInput" id="name" /><br />
    <label for="left">左上角空位数</label>
    <input type="number" v-model="leftnum" id="left" /><br />
    <label for="right">右上角空位数</label>
    <input type="number" v-model="rightnum" id="right" /><br />
    <button @click="gen(column)">生成座位表</button>
  </div>
  <div class="container" v-if="personName">
    <div v-for="(i,index) in personName" :key="i" class="person"><img :src="require(`../assets/img/${index+1}.jpg`)" alt="" />{{ i }}</div>
  </div><div id="podium">讲台</div>
</template>

<script>
export default {
  name: "StudentsTable",
  data() {
    return {
      column: 6,
      leftnum:0,
      rightnum:0,
      personPos: [],
      personName: [],
      posInput:"",
      //4 5 2 6 12 25 9 3 15 10 20 17 43 19 14 28 34 27 13 7 22 39 8 11 44 45 37 42 36 35 38 29 18 26 24 16 23 21 1 33 41 30 31 32 40
      nameInput:"",
      //张耘溪 运沛然 吴梁睿 马奕涵 包尔豪 席星野 张知航 胡家熙 杜雨航 李仰震 肖正阳 黄嘉祺 王尉沣 毕云飞 贾惠翔 杨毅群 那博宇 任津喆 魏吉辰 张鎏麟 张展赫 李京 李燕坤 杨盛钧 张浩 张竣词 郭昱嘉 刘宏坤 齐婧然 陶雨霏 王曼伊 孟慧 盖君仪 臧浩汐 石怡 何雨恬 牟可欣 谭思宇 路欣怡 田孝娜 孟子钧 吕晴 何诗楠 严佳兴 孙佳忆
    };
  },
  methods: {
    gen(column) {
      document.querySelector("#podium").style.display="block"
      document.querySelector(".settings").style.display="none"
      const container = document.querySelector(".container");
      container.style.gridTemplateColumns = `repeat(${column},100px)`;
      container.style.visibility = "unset";
      let item = document.querySelectorAll(".person");
      for (let i = 0; i < item.length; i++) {
        let num = this.personPos[i];
        item[num-1].style.order = i.toString();
      }

for (let i = 0; i < this.leftnum; i++) {
let div=document.createElement("div")
div.classList="person"
div.style.order=-1;
div.style.border="1px black solid"
container.append(div)
}
for (let i = 0; i < this.rightnum; i++) {
let div=document.createElement("div")
div.classList="person"
div.style.order=this.column-this.leftnum-1-this.rightnum
div.style.border="1px black solid"
container.append(div)
}

    },
      
  },
  watch: {
    nameInput() {
      this.personName = this.nameInput.split(" ");
    },
    posInput() {
      this.personPos = this.posInput.split(" ");
    },
  },
};
</script>

